 .card{
     width:432px;
     height:450px;
     margin:0 auto;
     position: relative;
     background-color: #fff;
     .header{
         width:100%;
         height:120px;
         padding-top:20px;
        //  border:1px solid red;
         div{
             height:50px;
             line-height:50px;
            //  border:1px solid red;
             display:block;
            //  width:100%;
             &:nth-of-type(1){
                 font-size: 30px;
                 font-weight: 700;
                
             }
             &:nth-of-type(2){
                font-size: 20px;
                font-weight: 400;
             }
         }
     }
     .table{
        padding:0 30px;
        .inputItem{
          height:60px;
          display: flex;
          border-bottom:1px solid #d9d9d9;
        //   border:1px solid red;
        .select{
            flex:2;
            margin:10px;
            border:none;
          .ant-select-selection{
              border:none;
          }
        }
        input{
            height:59px;
            border:none;
           &:focus{
               box-shadow: none;
             
           }
         }
        }
        button{
            width:80%;
            height:35px;
            margin:40px 0;
        }
     }
     .text{
         color:#b2b2b2;
     }
     .footer{
         height:60px;
         width:100%;
         background-color: #f6f6f6;
         position: absolute;
         bottom:0;
         right:0;
         border:1px solid #ebebeb;
         line-height:60px;
         span{
             cursor: pointer;
             &:nth-of-type(2){
                color:#0084ff;
             }
         }
       
     }
 }
}