.topic{
    background:#f6f6f6;
    .wrapper{
        width:1000px;
        // border:1px solid red;
        margin:0 auto;
        display: flex;
      
        .leftSide{
          flex-basis:700px;
          margin-right:10px;
          background: #fff;
          .cardItem{
            //   margin-bottom:5px;
          }
        }
        .rightSide{
            flex:1;
            margin-left: 20px;
            // border:1px solid #000;
            .card{
              height:150px;
              background: #fff;
              // display: flex;
              padding:20px 10px;
           
            .globalWrite-nav{
              display: inline-block;
              width:33.33%;
              color:#8590a6;
              font-size:16px;
              span{
               margin-top:5px;
               font-size:17px;
               display:block;
               color:#444;
              }
            }
            .globalWrite-draft{
              width:100%;
              height:50px;
              border-top:1px solid #eee;
              margin-top:25px;
              line-height: 50px;
              text-align: left;
              font-size:14px;
              font-weight: 600;
              span{
                display: inline-block;
                margin-left:10px;
              }
            }
          }
          .IMG{
            img{
              width:100%;
            }
          }
        }
    }
}