.store{
    width:1000px;
    margin:0 auto;
    list-style: none;
    ul{
        display:flex;
        // border:1px solid red;
        li{
            list-style: none; 
            flex:1;
            text-align:center;
            height:50px;
            border-bottom:1px solid #8590a6;
            line-height:50px;
            // border-radius: 15px;
            &:hover{
                background-color: rgba(0, 132, 255, 0.4);
                 border-radius: 15px;
                a{
                    color:#fff;
                } 
            }
        }
    }
   
}