.rent {
  min-width: 1000px;
  width: 90%;
  margin: 0 auto;
  background-color: #f8f8f8;
  .selectList {
    padding: 0 30px;
    // border: 1px solid red;

    .line {
      height: 85px;
      text-align: left;
      padding: 26px;
      border-bottom: 1px solid #eee;
      a {
        margin: 0 10px;
        height: 33px;
        font-size: 16px;
        display: inline-block;
        color: #999;
        margin: 0 20px;
        &:hover {
          color: #ff5c38;
        }
      }
      .title {
        background-color: #7f7f8b;
        border: none;
        border-radius: 16px 0 16px 16px;
        color: #fff;
        height: 35px;
        width: 60px;
        margin-right: 20px;
      }
    }
  }
  .content {
    margin-top: 30px;
    // border: 1px solid red;
    padding: 0 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    .boxItem {
      margin: 0 20px 70px;
      position: relative;
      border:1px solid #eee;
      transition: all 0.5s;
      background: #fff;
      border-radius: 5px;
      .card {
        // flex-basis: 250px;
        height: 300px;
        width: 220px;
        // border: 1px solid red;
        img {
          width: 100%;
        }
        .info{
           font-size:16px;
           font-weight: 700;
           margin-top:20px;
           .title{

           }
           .desc{
            font-size:15px;
            color:#999;
            font-weight: 300;
            margin:10px 15px;

           }
        }
      }
      .alterCard {
        visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
        button{
          position: fixed;
          bottom:30px;
          left:60px;
        }
      }
      &:hover .alterCard {
        transform: scale(1.1, 1.1);
        visibility: visible;
      }
    }
  }
}
.current {
  font-weight: 700;
  color: #ff5c38;
  background-color: #f7eae8;
  width: 60px;
  border-radius: 20px;
}
